<template>
  <section>
    <!--工具条-->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input placeholder="搜索" v-model="filters.name"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="filterData">查询</el-button>
        </el-form-item>
        <div class="block" style="float: right;margin-right:10px;">
          <el-button type="primary" @click="imports">批量导入</el-button>
        </div>
      </el-form>
    </el-col>

    <!--列表-->
    <el-table :data="topics" highlight-current-row v-loading="listLoading" style="width: 100%;">
      <el-table-column prop="cid" label="货号" align="center" width="230">
      </el-table-column>
      <el-table-column prop="firstCategory" label="一级类别" align="center" width="130">
      </el-table-column>
      <el-table-column prop="secondCategory" label="二级类别" align="center" width="130">
      </el-table-column>
      <el-table-column prop="name" label="物品名称" align="center" width="140">
      </el-table-column>
      <el-table-column label="单价" align="center" width="140">
        <template slot-scope="scope">
          {{ String(scope.row.price).split("-")[1] ? String(scope.row.price).split("-")[1].split(",")[0] : scope.row.price}}
        </template>
      </el-table-column>
      <el-table-column prop="createTime" label="新增时间" align="center" width="200">
      </el-table-column>
      <el-table-column label="有效天数" align="center" width="100">
        <template slot-scope="scope">
          {{scope.row.validTime}}天
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" min-width="220">
        <template slot-scope="scope">
          <el-button type="info" size="small" @click="handleShow(scope.row)" v-show="roles.indexOf('01020201') >= 0">查看
          </el-button>
          <el-button type="danger" size="small" @click="handleOutLine(scope.row)" v-show="roles.indexOf('01020202') >= 0">上架
          </el-button>
          <el-button type="danger" size="small" @click="handleEdit(scope.row)" v-show="roles.indexOf('01020203') >= 0">修改
          </el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.row)" v-show="roles.indexOf('01020204') >= 0">删除
          </el-button>
        </template>
      </el-table-column>
    </el-table>

    <!--工具条-->
    <el-col :span="24" class="toolbar" v-show="total > 0">
      <el-pagination layout="prev, pager, next" :current-page.sync="page" @current-change="handleCurrentChange" :page-size="pageSize" :total="total * pageSize" style="float:right;">
      </el-pagination>
    </el-col>

    <!--查看界面-->
    <el-dialog title="查看" v-model="showFormVisible" :close-on-click-modal="false">
      <el-form :model="showForm" label-width="120px">
        <el-form-item label="图片：" v-show="showForm.img.length">
          <div class="block" style="width: 300px;height: 300px;">
            <el-carousel trigger="click">
              <el-carousel-item v-for="(item,index) in showForm.img" :key="index">
                <img :src="item.url" style="width: 100%;height: 100%;">
              </el-carousel-item>
            </el-carousel>
          </div>
        </el-form-item>
        <el-form-item label="视频：" v-show="!showForm.img.length">
          <video :src="showForm.video.url" controls></video>
        </el-form-item>
        <!--
        <el-form-item label="货号：">
           <div>{{showForm.cid}}</div>
         </el-form-item>
         -->
        <el-form-item label="名字：">
          <div>{{showForm.name}}</div>
        </el-form-item>
        <el-form-item label="价格：">
          <div>{{ String(showForm.price).split("-")[1] ? String(showForm.price).split("-")[1].split(",")[0] : showForm.price}}</div>
        </el-form-item>
        <el-form-item label="颜色：">
          <div>{{showForm.color}}</div>
        </el-form-item>
        <el-form-item label="尺寸：">
          <div>{{showForm.size}}</div>
        </el-form-item>
      </el-form>
    </el-dialog>

    <!--修改界面-->
    <el-dialog title="修改" v-model="editFormVisible" :close-on-click-modal="false">
      <el-row :gutter="24">
        <el-form :model="editForm" label-width="120px" :rules="rules" ref="editForm">
          <el-col :span="12">
            <el-form-item label="商品名称" prop="name">
              <el-input v-model="editForm.name"></el-input>
            </el-form-item>
          </el-col>          
          <el-col :span="12">
            <el-form-item label="商品数量区段" prop="pcnumber" class="sector">
              <el-col :span="7">
                <el-input v-model="editForm.pcnumber[0]" :disabled="true"></el-input>
              </el-col>
              <el-col :span="1">-</el-col>
              <el-col :span="7">
                <el-input v-model="editForm.pcnumber[1]"></el-input>
              </el-col>
              <el-col :span="1">-</el-col>
              <el-col :span="7">
                <el-input v-model="editForm.pcnumber[2]"></el-input>
              </el-col>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="上架天数" prop="validTime">
              <el-input v-model="editForm.validTime">
                <template slot="append">天</template>
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="商品单价区段" prop="pcprice" class="sector">
              <el-col :span="7">
                <el-input v-model="editForm.pcprice[0]"></el-input>
              </el-col>
              <el-col :span="1">-</el-col>
              <el-col :span="7">
                <el-input v-model="editForm.pcprice[1]"></el-input>
              </el-col>
              <el-col :span="1">-</el-col>
              <el-col :span="7">
                <el-input v-model="editForm.pcprice[2]"></el-input>
              </el-col>
            </el-form-item>
          </el-col>          
          <el-col :span="12">
            <el-form-item label="圈口" prop="size">
              <el-input type="number" v-model="editForm.size"></el-input>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="商品颜色" prop="color">
              <el-select v-model="editForm.color" placeholder="请选择">
                <el-option v-for="item in colorOption" :key="item" :label="item" :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="一级类别" prop="firstId">
              <el-select placeholder="请选择" v-model="firstCategory" ref="firstSelect" @change="firstCate">
                <el-option v-for="item in firstCategoryOption" :key="item.firstId" :label="item.name" :value="item.firstId">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="二级类别" prop="secondId">
              <el-select v-model="secondCategory" :data-oldId="editForm.secondId" placeholder="请先选择一级类别">
                <el-option v-for="item in sectionCategoryOption" :key="item.secondId" :label="item.name" :value="item.secondId">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="是否上架">
              <el-radio class="radio" v-model="editForm.status" label="1">上架</el-radio>
              <el-radio class="radio" v-model="editForm.status" label="0">下架</el-radio>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="商品描述" prop="standarddcscribe">
              <el-input type="textarea" :autosize="{ minRows: 3, maxRows: 4}" placeholder="请输入内容" v-model="editForm.standarddcscribe">
              </el-input>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="视频/图片" prop="principal">
              <el-radio class="radio" v-model="videoOrPhotos" label="0">图片</el-radio>
              <el-radio class="radio" v-model="videoOrPhotos" label="1">视频</el-radio>
            </el-form-item>
          </el-col>
          <el-col :span="24" v-show="videoOrPhotos == 0">
            <el-form-item label="商品图片">
              <div style="color:#f00;font-weight: 600;">请上传750px * 750px 以上比例商品图片，图片和视频选一即可。</div>
              <el-upload :action="uploadServer+'/jade/image/uploadImage.action'" list-type="picture-card" :on-success="handleSuccess" multiple :before-upload="beforeUpload" :file-list="imgList" name="image" :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
              </el-upload>
              <el-dialog v-model="dialogVisible" size="tiny">
                <img width="100%" :src="dialogImageUrl" alt="">
              </el-dialog>
            </el-form-item>
          </el-col>
          <el-col :span="24" v-show="videoOrPhotos != 0">
            <el-form-item label="商品视频" prop="principal">
              <div style="color:#f00;font-weight: 600;">请上传播放时间为30s的商品视频，图片和视频选一即可</div>
              <el-upload class="upload-demo" drag :action="uploadServer+'/jade/image/uploadVideo.action'" name="image" :fileList="fileList" :before-upload="beforeVideoUpload" :on-success="handleSuccessVideo" style="width: 300px;">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                <div class="el-upload__tip" slot="tip">只能上传mp4文件，且不超过50MB</div>
              </el-upload>
              <video v-if="videoUrl" :src="videoUrl" class="avatar" controls></video>
            </el-form-item>
          </el-col>
          <el-col :span="24" v-show="videoOrPhotos != 0">
            <el-form-item label="视频封面">
              <el-upload class="avatar-uploader" :action="uploadServer+'/jade/image/uploadImage.action'" :show-file-list="false" name="image" :on-success="handleVideoPostSuccess">
                <img v-if="videoPostUrl" :src="videoPostUrl" class="avatar">
                <i v-else class="el-icon-plus avatar-uploader-icon"></i>
              </el-upload>
            </el-form-item>
          </el-col>
          <el-col :span="24">
            <el-form-item label="图文描述" prop="imageText">
              <el-upload :action="uploadServer+'/jade/image/uploadImage.action'" list-type="picture-card" :on-success="handleSuccess2" name="image" multiple :file-list="imgTextList" :on-remove="handleRemove2">
                <i class="el-icon-plus"></i>
              </el-upload>
              <el-dialog v-model="dialogVisible" size="tiny">
                <img width="100%" :src="dialogImageUrl" alt="">
              </el-dialog>
            </el-form-item>
          </el-col>
        </el-form>
      </el-row>
      <div class="footer" style="text-align: center">
        <el-button type="primary" @click.native="editAction">修改</el-button>
      </div>
      <el-row :gutter="24" style="padding-bottom: 0px;">
      </el-row>
    </el-dialog>

    <!--批量导入-->
    <el-dialog title="批量导入" size="tiny" v-model="importsVisible" :close-on-click-modal="false">
      <el-upload class="upload-demo" ref="upload" name="file" :action="uploadServer+'/jade/commodityManage/saveDoc.action?sid='+sid" :withCredentials="true" :before-upload="beforeImport" :on-success="handleImportSuccess" :on-remove="handleImportRemove" :auto-upload="false">
        <el-button slot="trigger" size="small" type="primary">选取文件</el-button>
        <el-button style="margin-left: 10px;" size="small" type="success" @click="submitUpload">上传到服务器</el-button>
        <a :href="server+'/jade/commodityManage/downExcel.action'" target="_blank" style="color: #fff;">
          <el-button style="margin-left: 10px;" size="small" type="success">下载模板</el-button>
        </a>
        <div slot="tip" class="el-upload__tip">可以上传xlsx2003和2007版本的文件但不要强制修改后缀名否则会导致版本冲突</div>
      </el-upload>
    </el-dialog>
  </section>
</template>

<script>
import {
  selectCommosityByCid,
  getCommoditysoldOut,
  soldOutAndPutAway,
  delCommodity,
  getCondition,
  updataCommodity,
  getFirstCategory,
  getSecondByFirst
} from 'api/goods';
import { getToken } from '@/utils/auth';
import { uploadServer, server } from 'api/config';

export default {
  data() {
    return {
      filters: {
        name: ''
      },
      totalData: [],
      topics: [],
      total: 0,
      page: 1,
      pageSize: 10,
      uploadServer: uploadServer,
      server: server,
      listLoading: false,
      showFormVisible: false, // 查看界面是否显示
      showLoading: false,
      showForm: {
        cid: '',
        color: '',
        name: '',
        price: '',
        pcnumber: ['', '', ''],
        pcprice: ['', '', ''],
        sid: '',
        size: '',
        img: [{ name: '', url: '' }],
        video: { name: '', url: '' }
      },
      editFormVisible: false, // 查看界面是否显示
      editLoading: false,
      dialogImageUrl: '',
      value: '',
      videoOrPhotos: '',
      dialogVisible: false,
      firstCategoryOption: [],
      firstCategory: '',
      sectionCategoryOption: [],
      sectionCategoryOptionFlag: true,
      secondCategory: '',
      waterOption: [],
      colorOption: [],
      editForm: {
        name: '',
        size: '',
        price: '',
        pcnumber: ['0', '', ''],
        pcprice: ['', '', ''],
        firstId: '',
        secondId: '',
        water: '',
        color: '',
        describe: '',
        imageText: '',
        img: '',
        video: '',
        status: '0'
      },
      rules: {
        firstId: [{ required: true, message: '请输入一级类别', trigger: 'blur' }],
        name: [{ required: true, message: '请输入商品名称', trigger: 'blur' }],
        size: [{ required: true, message: '请输入圈口', trigger: 'blur' }],
        price: [
          { required: true, message: '请输入价格', trigger: 'blur' },
          { min: 1, max: 8, message: '不能超过8位数', trigger: 'blur' }
        ],
        water: [{ required: true, message: '请选择种水', trigger: 'blur' }],
        color: [{ required: true, message: '请选择颜色', trigger: 'blur' }],
        standarddcscribe: [{ required: true, message: '请输入内容', trigger: 'change' }],
        imageText: [{ required: true, message: '请插入图文', trigger: 'change' }]
      },
      roles: sessionStorage.getItem('rules'),
      sid: getToken(),
      imgList: [],
      videoPostUrl: '',
      videoUrl: '',
      fileList: [],
      imgTextList: [],
      uploadVideoPost: '',
      videoLoading: false,
      importsVisible: false // 批量导入显示
    };
  },
  methods: {
    getTopics() {
      this.listLoading = true;
      getCommoditysoldOut().then(res => {
        if (res.success) {
          this.setTopics(res.result.data);
        } else {
          this.$confirm(res.result.error, '提示', {
            type: 'warning'
          }).then(() => {
            // do...
          });
        }
        this.listLoading = false;
      });
    },
    setTopics(data) {
      this.totalData = data.reverse();
      this.total = Math.ceil(this.totalData.length / 10);
      this.topics = data.slice(this.page - 1, 10);
    },
    filterData() {
      let ret = [];
      getCommoditysoldOut().then(res => {
        if (res.success) {
          this.setTopics(res.result.data);
          // 如果filters条件是空  则正常显示
          if (!this.filters.name) {
            return;
          }
          // 不然按照数据显示
          this.totalData.map((item, index) => {
            for (let val in item) {
              if (ret.indexOf(val) < 0) {
                let tmp = item[val] + '';
                if (tmp.indexOf(this.filters.name) >= 0) {
                  ret.push(item);
                  break;
                }
              }
            }
          });
          this.setTopics(ret);
        }
      });
    },
    handleCurrentChange(val) {
      this.page = val;
      let ret = [];
      for (let i = (val - 1) * 10; i < 10 * val; i++) {
        if (this.totalData[i]) {
          ret.push(this.totalData[i]);
        }
      }
      this.topics = ret;
    },
    handleOutLine(row) {
      this.$confirm('确认上架吗?', '提示', {
        type: 'success'
      })
        .then(() => {
          this.listLoading = true;
          let para = JSON.stringify({ cid: row.cid });
          soldOutAndPutAway(para).then(res => {
            this.listLoading = false;
            if (res.success) {
              this.$message({
                message: '上架成功',
                type: 'success'
              });
            } else {
              this.$message.error('上架失败');
            }
            this.getTopics();
          });
        })
        .catch(() => {});
    },
    handleEdit(row) {
      this.listLoading = true;
      this.sectionCategoryOptionFlag = true;
      getFirstCategory({}).then(res => {
        this.firstCategoryOption = res.result.data;
        getCondition().then(res => {
          this.imgList = [];
          this.videoUrl = '';
          this.videoPostUrl = '';
          this.uploadVideoPost = '';
          this.imgTextList = [];
          this.waterOption = res.result.water;
          this.colorOption = res.result.color;
          Object.assign(this.editForm, row, { status: '1' });
          if (row.figuredescribe) {
            row.figuredescribe.map(item => {
              this.imgTextList.push({
                name: item.key,
                url: item.url
              });
            });
          }
          let pcnumber = this.editForm.price.split('-')[0].split(',');
          let pcprice = this.editForm.price.split('-')[1].split(',');
          this.editForm.pcnumber = pcnumber;
          this.editForm.pcprice = pcprice;
          this.editForm.color = row.color;
          if (row.isNoPackagemaill !== '0') {
            this.videoUrl = row.isNoPackagemaill[0].url;
            this.videoPostUrl = row.isNoPackagemaill[1].url;
            this.editForm.video = row.isNoPackagemaill[0].key;
            this.uploadVideoPost = row.isNoPackagemaill[1].key;
            if (row.commodityImg) {
              row.commodityImg.map(item => {
                this.imgList.push({ name: item.key, url: item.url });
              });
            }
          } else {
            row.commodityImg.map(item => {
              this.imgList.push({ name: item.key, url: item.url });
            });
          }
          this.videoOrPhotos = this.editForm.videoOrPhotos;
          this.firstCategory = this.editForm.firstCategory;
          this.secondCategory = this.editForm.secondCategory || '其它';
          this.fileList = [];
          this.firstCate(this.firstCategory);
          this.editFormVisible = true;
          this.listLoading = false;
        });
      });
    },
    firstCate(val) {
      if (!val) {
        // 如果val为空返回
        return false;
      }
      if (val === this.editForm.firstCategory) {
        // 如果val的值是一文字  就让其变成对应的id
        val = this.editForm.firstId;
      }
      let param = {
        firstId: val
      };
      getSecondByFirst(param).then(res => {
        this.sectionCategoryOption = res.result.data;
        this.sectionCategoryOptionFlag = false;
        if (this.sectionCategoryOption[0].name === '其它' || this.sectionCategoryOption[0].name === '其他') {
          // 没有其他项的话就默认选中其它
          this.secondCategory = this.sectionCategoryOption[0].name;
        } else {
          var flag = false; // 判断是不是在原来的项中
          for (var i = 0; i < this.sectionCategoryOption.length; i++) {
            if (this.secondCategory === this.sectionCategoryOption[i].name) {
              flag = true;
            }
          }
          if (flag) {
            this.secondCategory = this.editForm.secondCategory;
          } else {
            this.secondCategory = this.sectionCategoryOption[0].name;
          }
        }
      });
    }, // 点击一级二级出来
    getFirst() {
      this.listLoading = true;
      getFirstCategory({}).then(res => {
        this.firstCategoryOption = res.result.data;
        this.listLoading = false;
      });
    },
    handleShow(row) {
      this.listLoading = true;
      let params = JSON.stringify({
        cid: row.cid
      });
      selectCommosityByCid(params).then(res => {
        Object.assign(
          this.showForm,
          {
            img: [],
            video: {}
          },
          res.result
        );
        this.showFormVisible = true;
        this.listLoading = false;
      });
    },
    handleDel(row) {
      this.$confirm('确认删除吗?', '提示', {
        type: 'warning'
      })
        .then(() => {
          this.listLoading = true;
          let para = JSON.stringify({ cid: row.cid });
          delCommodity(para).then(res => {
            this.listLoading = false;
            if (res.success) {
              this.$message({
                message: '删除成功',
                type: 'success'
              });
            } else {
              this.$message.error('删除失败');
            }
            this.handleCurrentChange(1);
            this.getTopics();
          });
        })
        .catch(() => {});
    },
    beforeUpload(file) {
      if (file.size > 6 * 1024 * 1024) {
        this.$message({
          type: 'error',
          message: '图片不可以超过6MB一张',
          duration: 1000
        });
        return false;
      }
      if (this.imgList.length > 8) {
        this.$message({
          type: 'error',
          message: '最多上传9张图片',
          duration: 1000
        });
        return false;
      }
    },
    handleSuccess(res, file, fileList) {
      this.imgList = fileList;
    },
    handleRemove(file, fileList) {
      this.imgList = fileList;
    },
    handleSuccess2(response, file, fileList) {
      this.imgTextList = fileList;
    }, // 图文描述新增
    handleRemove2(file, fileList) {
      this.imgTextList = fileList;
    }, // 图文描述删除
    editAction() {
      // 视频和图片
      if (this.videoOrPhotos === '1') {
        // 视频
        if (!this.editForm.video) {
          this.$message({
            showClose: true,
            message: '请选择要上传的视频',
            type: 'error'
          });
          return;
        } else {
          if (!this.uploadVideoPost) {
            this.$message.error('请先上传视频封面');
            return;
          }
          this.editForm.video.indexOf('.mp4') > 0 ? (this.editForm.video = this.editForm.video.slice(0, -4)) : '';
          this.editForm.isNoPackagemaill = this.editForm.video + ',' + this.uploadVideoPost;
          let list = [];
          if (this.imgList.length) {
            this.imgList.map(item => {
              if (item.name) {
                list.push(item.name);
              } else {
                list.push(item.response.result.key);
              }
            });
          }
          this.editForm.commodityImg = list.toString();
        }
      } else {
        // 图片
        //          if (this.imgList.length < 3) {
        //            this.$message({
        //              showClose: true,
        //              message: '请上传至少3张的图片',
        //              type: 'error'
        //            })
        //            return
        //          }
        let commodityImg = [];
        this.imgList.map((item, index) => {
          if (index < 9) {
            if (item.response) {
              commodityImg.push(item.response.result.key);
            } else {
              commodityImg.push(item.name);
            }
          }
        });
        this.editForm.commodityImg = commodityImg.join(',');
        this.editForm.video.indexOf('.mp4') > 0 ? (this.editForm.video = this.editForm.video.slice(0, -4)) : '';
        if (!this.uploadVideoPost) {
          if (this.editForm.isNoPackagemaill !== '0') {
            this.editForm.isNoPackagemaill = this.editForm.video + ',' + this.editForm.video[1].key;
          } else {
            this.editForm.isNoPackagemaill = '0';
          }
        } else {
          this.editForm.isNoPackagemaill = this.editForm.video + ',' + this.uploadVideoPost;
        }
      }
      let imageText = [];
      this.imgTextList.map((item, index) => {
        if (item.response) {
          imageText.push(item.response.result.key);
        } else {
          imageText.push(item.name);
        }
      }); // 处理文字描述
      this.editForm.figuredescribe = imageText.join(','); // 图片描述图片用,连接
      this.editLoading = true;
      this.editForm.size = (this.editForm.size - 0).toFixed('1'); // 价格
      this.editForm.videoOrPhotos = this.videoOrPhotos; // 视频还是图片
      // 处理分类
      this.editForm.firstId =
        this.firstCategory === this.editForm.firstCategory ? this.editForm.firstId : this.firstCategory;
      if (this.secondCategory === this.sectionCategoryOption[0].name) {
        // 如果是默认选中的二级类别就选中其第一个id
        this.editForm.secondId = this.sectionCategoryOption[0].secondId;
      } else {
        if (this.secondCategory === '其它' || this.secondCategory === '其他') {
          // 如果是其他的话就让它变成id
          this.editForm.secondId = this.sectionCategoryOption[0].secondId;
        } else {
          var flag = false; // 判断是不是在原来的项中
          for (var i = 0; i < this.sectionCategoryOption.length; i++) {
            if (this.secondCategory === this.sectionCategoryOption[i].name) {
              flag = true;
            }
          }
          if (!flag) {
            this.editForm.secondId = this.secondCategory;
          }
        }
      }
      this.editForm.price = this.editForm.pcnumber.join(',') + '-' + this.editForm.pcprice.join(',');
      let para = JSON.stringify(this.editForm);
      updataCommodity(para).then(res => {
        this.editLoading = false;
        if (res.success) {
          this.$message({
            message: '更新成功',
            type: 'success'
          });
        } else {
          this.$message.error(res.result.error);
        }
        this.editFormVisible = false;
        this.getTopics();
        this.handleCurrentChange(this.page);
      });
    }, // 提交修改
    beforeVideoUpload(file) {
      const isMP4 = file.type === 'video/mp4';
      const isLt50M = file.size / 1024 / 1024 < 50;

      if (!isMP4) {
        this.$message.error('上传视频只能是MP4格式!');
      }
      if (!isLt50M) {
        this.$message.error('上传视频大小不能超过 50MB!');
      }
      isMP4 && isLt50M ? (this.videoLoading = true) : '';
      return isMP4 && isLt50M;
    }, // 上传视频之前
    handleSuccessVideo(response, file) {
      this.videoLoading = false;
      this.videoUrl = file.url;
      this.editForm.video = response.result.key;
      this.editForm.img = '';
    }, // 上传视频完成
    handleImportSuccess(res) {
      if (res.success) {
        this.$message({
          message: '恭喜你 上传成功!!!',
          type: 'success'
        });
        this.getTopics();
      } else {
        this.$message.error('上传失败');
      }
      this.importsVisible = false;
    },
    submitUpload() {
      this.$refs.upload.submit();
    },
    handleImportRemove() {},
    handleVideoPostSuccess(res, file) {
      this.uploadVideoPost = res.result.key;
      this.videoPostUrl = file.url;
    }, // 上传videoPost
    imports() {
      this.importsVisible = true;
    }, // 批量导入
    beforeImport(file) {
      if (!file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet') {
        this.$message.error('只能上传xlsx文件');
        return;
      }
    }
  },
  mounted() {
    this.getTopics();
  }
};
</script>

<style scope lang="stylus">
.add {
  width: 800px;

  .footer {
    text-align: center;
  }
}

.avatar-uploader .el-upload {
  border: 1px dashed #d9d9d9;
  border-radius: 6px;
  cursor: pointer;
  background: #fbfdff;
  position: relative;
  overflow: hidden;
}

.sector {
  .el-col-7 {
    padding-left: 0 !important;
    padding-right: 0 !important;
  }

  .el-col-1 {
    padding-left: 2px !important;
    padding-right: 0 !important;
  }
}

.avatar-uploader .el-upload:hover {
  border-color: #20a0ff;
}

.avatar-uploader-icon {
  font-size: 28px;
  color: #8c939d;
  width: 148px;
  height: 148px;
  line-height: 148px;
  text-align: center;
}

.avatar {
  width: 148px;
  height: 148px;
  display: block;
}
</style>



